import React, { useMemo } from 'react';
import { Input, Container, Progress } from 'reactstrap';
import { sumOf, bySemester, requiredCourses, sumRequiredOf } from './studyPlan';
import useLocalStorage from './useLocalStorage';
import config from './config.json';

export default function App() {
    const [selected, setSelected] = useLocalStorage(
        'selected',
        requiredCourses.map((x) => x.code),
    );

    const sum = useMemo(() => sumOf(selected), [selected]);
    const reqPercent = useMemo(() => sumRequiredOf(selected) / config.maxPoints * 100, [selected]);
    const addPercent = useMemo(() => (sumOf(selected) - sumRequiredOf(selected)) / config.maxPoints * 100, [selected]);

    const inputClick = (code) => {
        if (selected.includes(code)) {
            setSelected(selected.filter((c) => c !== code));
        } else {
            setSelected([...selected, code]);
        }
    };

    const courseView = (c) => (
      <div key={c.code}>
        <Input type="checkbox" id={c.code} checked={selected.includes(c.code)} onChange={() => inputClick(c.code)} />
        <label htmlFor={c.code}>М.{c.mod} - {c.name} - {c.points}</label>
      </div>
    );

    return (
      <div className="App">
        <header className="fixed-top bg-light">
          <Container className="d-flex">
            <h1 className="pr-5 my-header">Калькулятор ЗЕТ</h1>
            <h2 className="align-self-center">Результат: {sum} из {config.maxPoints}.</h2>
          </Container>
          <Progress multi className="rounded-0">
            <Progress bar color="danger" value={reqPercent} />
            <Progress bar color="warning" value={addPercent} />
          </Progress>
        </header>
        <Container className="mt-5">
          { bySemester && bySemester
              .map((x, i) => (
                <div key={i}>
                  <h2>Семестр {i + 1}</h2>
                  <div className="d-flex">
                    { x.some((c) => c.required) && (
                      <div>
                        <h3>Обязательные</h3>
                        {x.filter((c) => c.required).map(courseView)}
                      </div>
                    )}
                    { x.some((c) => !c.required) && (
                      <div>
                        <h3>Спец. курсы</h3>
                        {x.filter((c) => !c.required).map(courseView)}
                      </div>
                    )}
                  </div>
                </div>
              ))}
        </Container>

      </div>
    );
}
